.page {
  font-family: "Cinzel", serif;
  font-weight: 400;
  font-size: 16px;
  color: $blackColor;

  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__title {
    margin: 0 0 48px;

    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    text-align: center;
    letter-spacing: -1.5px;

    @include onPhone {
      font-size: 32px;
      line-height: 32px;
      margin: 0 0 40px;
    }
  }

  &__section {
    margin-bottom: 140px;

    @include onPhone {
      margin-bottom: 100px;
    }

    &--menu {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;

      transform: translateX(-100%);
      transition: transform 0.3s;

      &:target {
        transform: translateX(0);
      }
    }
  }
}
